import React,{useState} from 'react';
import Button from '@material-ui/core/Button';

const ContactForm = () =>{
    const [formState,setFormState]=useState({
        fname:'',
        lname:'',
        email:'',
        phoneno:'',
        desc:''
    });
    const formChangeHandler = (event) =>{
            const {name,value}=event.target;

            setFormState((prevValue)=>{
                    return{
                        ...prevValue,
                        [name]: value,
                    };
            });
    }
    const formSubmit = (event) =>{
        event.preventDefault();
        alert(`First Name is ${formState['fname']} , Last Name is ${formState['lname']} , email is ${formState['email']}, phoneno is ${formState['phoneno']}, desc is ${formState['desc']}`)
        
    }
    return(
        <div className="contactFormInputField">
            <div className="row1">
            <input type="text" name="fname" placeholder="*First Name" autoComplete="off" value={formState.fname} onChange={formChangeHandler} required/>
            <input type="text" name="lname" placeholder="*Last Name" autoComplete="off" value={formState.lname} onChange={formChangeHandler} required/>
            </div>
            <div className="row2">
            <input type="email" name="email" placeholder="*Email" autoComplete="off" value={formState.email} onChange={formChangeHandler} required/>
            <input type="text" name="phoneno" placeholder="Phone No" autoComplete="off" value={formState.phoneno} onChange={formChangeHandler}/>
            </div>
            <div className="row3">
            <textarea name="desc" placeholder="*Description" value={formState.desc} onChange={formChangeHandler}></textarea>
            </div>
            <div className="row4">
            <Button className="cardButton" color="Primary" variant="contained" onClick={formSubmit} >Submit</Button>
            </div>


        </div>
    )
}
export default ContactForm;