import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Button from '@material-ui/core/Button';
import work1 from './work1.PNG';



const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
    marginBottom: '20px', 
    marginLeft:'20px',
    marginRight:'20px',
    borderRadius:'20px',
    backgroundColor:'#FAFAFA'
  },
  media: { 
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    // backgroundColor: red[500],
  },
}));
function changeColorOfFavIcon(id){
  document.getElementById('favIcon').style='color:#1d8ba1';
  console.log(`Liked for ${id} `)
}

export default function PortCard(props) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
   
    <Card className={classes.root} >
      <CardHeader
        avatar={
          <Avatar aria-label="recipe" style={{backgroundColor:'#353838'}} className={classes.avatar}>
            {/* {props.avatarLogo} */}
            {props.id + 1}
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={props.title}
        subheader={props.date}
      />
      <CardMedia
        className={classes.media}
        image={require(`./${props.img}`)}
        title={props.title}
      />
      <CardContent>
        <Typography variant="body1" color="textSecondary" component="p">
          
          {props.desc}
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton id="favIcon" style={{color:'grey'}} onClick={ ()=> changeColorOfFavIcon(`${props.id}`)} aria-label="add to favorites">
          <FavoriteIcon />
        </IconButton>

        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
        </IconButton>

        <a href={props.link} target="_blank" style={{textDecoration:'none'}}><Button className="cardButton" variant="contained">View Code</Button></a>

        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
      </CardActions>

     

    </Card>
  );
}