import React from 'react';
import MyProfile from './MyProfile';
import MyDisplayPage from './MyDisplayPage';
import Menu from './Menu';
import NavBarMobile from './NavBarMobile';
import ContactForm from './ContactForm';
import fb from './Facebook-PNG-HD.png';
import insta from './Instagram-Free-PNG-Image.png';
import linkedin from './Linkedin-PNG-Picture.png';

const Contact = () =>{
    
    return(
        <>
        {/* <MyProfile/> */}
        {/* <MyDisplayPage text={"Contact Us Page"}/> */}
        <div className="page">
            <NavBarMobile/>
        <h1 className="title" style={{fontSize:'40px',color:'white',fontFamily:'Poppins',marginTop:'5%'}}>Contact</h1>

        <div className="contactFormMain">
            <div className="form">
            <ContactForm/>
            </div>
            <div className="formInfo">
                <div className="socialmedia"><img src={fb}  alt="fb" style={{width:'100%',height:'100%'}} /></div>
                <div className="socialmedia"><img src={insta} alt="instagram" style={{width:'100%',height:'100%'}} /></div>
                <div className="socialmedia"><img src={linkedin} alt="linkedin" style={{width:'100%',height:'100%'}} /></div>
            </div>
        </div>
            
        </div>
        {/* <Menu /> */}
        </>
    );
}
export default Contact;