import React from 'react';
// import Menu from './Menu';
import './NavBarMobile.css';
import HomeOutlinedIcon from '@material-ui/icons/HomeOutlined';
import PeopleAltOutlinedIcon from '@material-ui/icons/PeopleAltOutlined';
import NoteOutlinedIcon from '@material-ui/icons/NoteOutlined';
import ContactMailOutlinedIcon from '@material-ui/icons/ContactMailOutlined';
import MenuBookOutlinedIcon from '@material-ui/icons/MenuBookOutlined';
import {Link} from 'react-router-dom';


const NavBarMobile = () =>{
    return(
        <div className="hamburgerMenu"> 
             

                    <label for="toggle">&#9776;</label>
                    <input type="checkbox" id="toggle"/>

            <nav> 
        <Link to="/"> <HomeOutlinedIcon   fontSize="small" style={{width:'100%',height:'10%',paddingTop:'10px',color: 'white',paddingBottom:'5px',borderBottom:'2px white solid'}}/></Link>
        <Link to="/About">  <PeopleAltOutlinedIcon  fontSize="small"  style={{width:'100%',height:'10%',paddingTop:'10px',color: 'white',paddingBottom:'5px',borderBottom:'2px white solid'}}/></Link>
        <Link to="/Portfolio">  <NoteOutlinedIcon  fontSize="small"  style={{width:'100%',height:'10%',paddingTop:'10px',color: 'white',paddingBottom:'5px',borderBottom:'2px white solid'}}/></Link>
        <Link to="/Contact">  <ContactMailOutlinedIcon  fontSize="small"  style={{width:'100%',height:'10%',paddingTop:'10px',color: 'white',paddingBottom:'5px',borderBottom:'2px white solid'}}/></Link>
        <Link to="/Blog">  <MenuBookOutlinedIcon  fontSize="small"  style={{width:'100%',height:'10%',paddingTop:'10px',color: 'white',paddingBottom:'5px'}}/></Link>
            </nav>
        </div>
    ) 
}
export default NavBarMobile;